<template>
  <!-- <form action="your-action" @submit="onFormSubmit($event)"> -->
  <div class="dx-field">
    <div v-if="label" class="dx-field-label">
      <div
        v-if="iconLabel"
        :class="iconLabel"
        style="margin-right: 4px; float: left"
      ></div>
      {{ label }}<span v-if="required && label" style="color: red">(*)</span>
    </div>
    <div class="dx-field-value" :style="customStyle">
      <DxTextBox
        :ref="fieldName"
        :placeholder="placeholder"
        v-model="currentValue"
        :onFocusOut="onBlur"
        @value-changed="valueChanged"
        :read-only="stateShowForm == 'reviewForm' ? true : false"
      >
        <DxValidator v-if="validate">
          <DxRequiredRule v-if="required" message="Trng ny khng c php  trng" />
          <DxEmailRule v-if="typeEmail" message="Email is invalid" />
          <DxAsyncRule
            v-if="duplicate"
            :validation-callback="asyncValidation"
            message="Email is already registered"
          />
          <DxPatternRule
            v-if="notNumber"
            :pattern="namePattern"
            message="Trng ny khng c bao gm s"
          />
          <DxStringLengthRule
            v-if="maxLength"
            :max="20"
            message="Trng ny khng c qu 20 k t"
          />
        </DxValidator>
      </DxTextBox>
    </div>
    <div></div>
  </div>
</template>
<script>
import DxTextBox from "devextreme-vue/text-box";
import DxValidationSummary from "devextreme-vue/validation-summary";
import EmployeesAPI from "@/api/components/employees/EmployeesAPI.js";
import {
  DxValidator,
  DxRequiredRule,
  DxCompareRule,
  DxEmailRule,
  DxPatternRule,
  DxStringLengthRule,
  DxAsyncRule,
} from "devextreme-vue/validator";
const sendRequest = function (value) {
  const validEmail = "test@dx-email.com";
  return new Promise((resolve) => {
    setTimeout(function () {
      resolve(value === validEmail);
    }, 1000);
  });
};
export default {
  data() {
    return {
      namePattern: /^[^0-9]+$/,
      currentValue: this.objectData[this.fieldName],
      maxEmployeeCode: null,
    };
  },
  created() {
    // if(!this.currentValue){
    //   this.currentValue= null;
    // }
  },
  mounted() {
    //focus n v tr  input u tin
    if (this.fieldName == "EmployeeCode") this.$refs.EmployeeCode.$el.focus();

    //Ly m nhn vin ln nht

    //+ Kim tra trng input ny c phi l ca employeecode khng v form ang m c nhim v thm hay khng
    if (this.fieldName == "EmployeeCode" && this.stateShowForm == "addForm") {
      this.getMaxEmployeeCode();
    }
  },
  watch: {
    maxEmployeeCode: function () {
      this.currentValue = this.maxEmployeeCode;
    },
    objectData: function () {
      debugger;
      //Cp nht gi tr vo khi i tng objectData thay i
      this.currentValue = this.objectData[this.fieldName];

      //i vi trng input ca m nhn vin, cn tin hnh t ng tng  cp nht gi tr ln nht
      if (this.fieldName == "EmployeeCode" && (this.stateShowForm == "addForm" || this.stateShowForm == "saveAndAddForm")) {
        this.getMaxEmployeeCode();
      }
    },
  },
  components: {
    DxTextBox,
    DxValidator,
    DxRequiredRule,
    DxCompareRule,
    DxEmailRule,
    DxPatternRule,
    DxStringLengthRule,
    DxValidationSummary,
  },
  props: {
    label: {
      type: [String],
      default: null,
    },
    placeholder: {
      type: String,
      default: null,
    },
    validate: {
      type: Boolean,
      default: false,
    },
    required: {
      type: Boolean,
      default: false,
    },
    notNumber: {
      type: Boolean,
      default: false,
    },
    maxLength: {
      type: Boolean,
      default: false,
    },
    typeEmail: {
      type: Boolean,
      default: false,
    },
    duplicate: {
      type: Boolean,
      default: false,
    },
    objectData: {
      type: Object,
      default: null,
    },
    fieldName: {
      type: String,
      default: null,
    },
    customStyle: {
      type: String,
      default: null,
    },
    stateShowForm: {
      type: String,
      default: null,
    },
    iconLabel: {
      type: String,
      default: null,
    },
  },
  methods: {
    /**
     * Hm ly m nhn vin max v t ng tng
     * CreatedBy: HNANH(27/12/2020)
     */
    async getMaxEmployeeCode() {
      debugger
      var me = this;
      await EmployeesAPI.getMaxEmployeeCode()
        .then(function (res) {
          var maxCode = res.data;
          //Tin hnh o ngc m v t ng tng t cui ln u
          var reverseMaxCode = maxCode.split("").reverse().join("");
          var increased = false;
          //CHuyn chui v mng v dng map  tnh ton ra v tr m cn tng
          var newCode = reverseMaxCode.split("").map(function (x) {
            if (!increased) {
              var asciiCode = x.charCodeAt(0);
              if (
                (asciiCode < 57 && asciiCode >= 48) ||
                (asciiCode < 90 && asciiCode >= 65) ||
                (asciiCode < 122 && asciiCode >= 97)
              ) {
                increased = true;
                asciiCode++;
                return String.fromCharCode(asciiCode);
              } else {
                return 0;
              }
            }
            return x;
          });

          var resultCode = newCode.reverse().join("");
          me.currentValue = resultCode;
        })
        .catch(function (err) {});
    },
    /**
     * Hm x l s kin blur, mc ch  lm thay i gi tr t null sang ''  hm
     * validate ca th vin c th hiu l ang trng
     */
    onBlur() {
      debugger;
      if (!this.currentValue) {
        this.currentValue = "";
      }
    },
    /**
     * Hm validate ng email ca th vin devextreme
     */
    asyncValidation(params) {
      return sendRequest(params.value);
    },
    /**
     * Hm x l s kin khi ni dung input c s thay i
     * CreatedBy: HNANH(20/12/2020)
     */
    valueChanged() {
      debugger;
      var fieldName = this.fieldName;
      var value = this.currentValue ? this.currentValue.toString().trim() : null;
      if (value == "") {
        value = null;
      }
      this.objectData[fieldName] = value;

      this.$emit("getValue", this.objectData);
    },
  },
};
</script>
<style>
/* Css ghi  li css ca th vin */
.dx-field-value {
  width: calc(100% - 150px) !important;
}
.dx-field-label {
  width: 150px;
}
</style>



