<template>
  <div class="dx-field">
    <div class="dx-field-label">
      {{ label }}<span v-if="required" style="color: red">(*)</span>
    </div>
    <div class="dx-field-value">
      <DxDropDownBox
        v-model="treeBoxValue"
        :show-clear-button="true"
        :data-source="treeDataSource"
        value-expr="DepartmentId"
        display-expr="DepartmentName"
        :placeholder="placeholder"
        @value-changed="syncTreeViewSelection($event)"
        :onFocusOut="onBlur"
        :read-only="(stateShowForm == 'reviewForm')? true: false"
      >
        <DxValidator v-if="validate">
          <DxRequiredRule v-if="required" message="Trng ny khng c php  trng" />
        </DxValidator>
        <template #content="{ data }">
          <DxTreeView
            :ref="treeViewRefName"
            :data-source="treeDataSource"
            :select-by-click="true"
            data-structure="plain"
            key-expr="DepartmentId"
            parent-id-expr="CategoryId"
            selection-mode="single"
            display-expr="DepartmentName"
            @content-ready="$event.component.selectItem(treeBoxValue)"
            @item-selection-changed="treeView_itemSelectionChanged($event)"
          />
        </template>
      </DxDropDownBox>
    </div>
  </div>
</template>
<script>
import DxDropDownBox from "devextreme-vue/drop-down-box";
import DxTreeView from "devextreme-vue/tree-view";
import DataSource from "devextreme/data/data_source";
import DxValidationSummary from "devextreme-vue/validation-summary";
import { DxValidator, DxRequiredRule } from "devextreme-vue/validator";
export default {
  data() {
    return {
      treeDataSource: null,
      treeBoxValue: '',
      treeViewRefName: "tree-view",
      inputDatas: [],
    };
  },
  props: {
    label: {
      type: String,
      default: "",
    },
    datas: {
      type: Array,
      default: [],
    },
    placeholder: {
      type: String,
      default: null,
    },
    validate: {
      type: Boolean,
      default: false,
    },
    required: {
      type: Boolean,
      default: false,
    },
    objectData: {
      type: Object,
      default: null,
    },
    fieldName: {
      type: String,
      default: null,
    },
    stateShowForm: {
      type: String,
      default: null,
    }
  },
  watch: {
    /**
     * Kim tra s thay i ca d liu khi to ban u nhn c t cha, con hng  prop
     * Khi ngi dng nhn ct v thm objectData  cha b khi to v null,  con phi kim tra
     * thay i v gn li gi tr ban u cho mnh
     */
    objectData: async function(){
      await this.getDataDepartment();
      this.treeDataSource =  await this.makeDataSource();
      this.selectDepartment();
    },
    /**
     * Kim tra s thay i ca d liu nhn c t cha, con hng  prop
     */
    datas:async function(){
      await this.getDataDepartment();
      this.treeDataSource =  await this.makeDataSource();
      await this.selectDepartment();
    }
  },
  components: {
    DxDropDownBox,
    DxTreeView,
    DxValidator,
    DxRequiredRule,
  },
  methods: {
    /**
     * Hm chn ng phng ban tng ng vi phng ban ca i tng objectData nhn c t cha
     * CrearedBy: HNANH(20/12/2020)
     */
    selectDepartment() {
      debugger
      var inputValue = this.objectData[this.fieldName];
      var inputData = this.inputDatas.find(function (data, index) {
        return (data["DepartmentName"] == inputValue);
      });
      if (inputData) {
        this.treeBoxValue = inputData["DepartmentId"]
      } else {
        this.treeBoxValue = '';
      }
    },
    /**
     * Hm ly data cc d liu m ngi dng c th chn
     * CrearedBy: HNANH(20/12/2020)
     */
    makeDataSource() {
      return new DataSource({
        store: this.datas,
        map: function (item) {
          item.key = item["DepartmentId"];
          return item;
        },
      });
    },
    /**
     * Hm x l s kin khi ngi dng chn v s hin thi ra ng tn d liu 
     * CrearedBy: HNANH(20/12/2020)
     */
    syncTreeViewSelection() {
      // debugger
      if (!this.$refs[this.textBoxRefName]) return;
      if (!this.treeBoxValue) {
        this.$refs[this.textBoxRefName].instance.unselectAll();
      } else {
        this.$refs[this.textBoxRefName].instance.selectItem(this.treeBoxValue);
      }
    },
    /**
     * Hm x l s kin  khi c thay i v tin hnh gi tr li i tng sau khi  c cp nht thm d liu
     * CrearedBy: HNANH(20/12/2020)
     */
    treeView_itemSelectionChanged(e) {
      this.treeBoxValue = e.component.getSelectedNodeKeys();
      var fieldName = this.fieldName;
      this.objectData[fieldName] = e.itemData.DepartmentName;
      this.objectData['DepartmentId'] = e.itemData.DepartmentId;
      this.$emit("getValue", this.objectData);
    },
    getDataDepartment(){
      this.inputDatas= this.datas;
    },
    /**
     * X l s kin blur, chun b cho vic validate bt buc nhp
     */
    onBlur(){
      if (this.treeBoxValue == ''){
        this.treeBoxValue = null;
      }
    }
  },
};
</script>
<style>
/* Css ghi  ln css ca th vin */
.dx-treeview-item.dx-state-hover {
  background-color: #e7f4ff !important;
}
.dx-treeview-item-without-checkbox.dx-state-focused > .dx-treeview-item {
  background-color: #cfe9ff !important;
}
.dx-treeview-item-without-checkbox.dx-state-selected > .dx-treeview-item {
  background-color: #cfe9ff !important;
}
.dx-dropdowneditor-button.dx-state-active .dx-dropdowneditor-icon,
.dx-dropdowneditor.dx-dropdowneditor-active .dx-dropdowneditor-icon {
  background-color: #ffffff;
}
.dx-placeholder::before {
  padding: 0;
  padding-left: 9px;
  line-height: 30px;
}
.dx-show-clear-button .dx-icon-clear {
  color: #ffffff !important;
}
</style>



