import Vue from 'vue';
import VueI18n from 'vue-i18n';

Vue.use(VueI18n);

const messages = {
    'en': {},
    'vi': {
        messAddSuccess: 'Thng tin nhn vin  c thm thnh cng',
        messEditSuccess: 'Thng tin nhn vin  c sa thnh cng',
        messDeleteSuccess: 'Thng tin nhn vin  c sa thnh cng',
        confirmDelete: "Bn c thc s mun xa nhn vin  chn khng?",
        messDeleteNotChooseRecord: 'Vui lng chn bn ghi mun xa',
        messEditNotChooseRecord: 'Vui lng chn bn ghi mun xem v sa',
        titleFormAddEmployee: "Thm mi h s",
    }
};

const i18n = new VueI18n({
    locale: 'vi', // set locale
    fallbackLocale: 'vi', // set fallback locale
    messages, // set locale messages
});

export default i18n;


